import React, { useState } from "react";
import "./Skill.css";

//https://codepen.io/ste-vg/pen/wdBRZN

function Skill(props) {
  const [details, setDetails] = useState("");
  const [tilted, setTilted] = useState(false);

  const tilt = () => {
    if (!tilted) {
      setDetails("");
      setTilted(true);
      console.log("I am tilting");
    } else {
      setDetails("details");
      setTilted(false);
      console.log("I am untilting");
    }

    return 0;
  };

  const frameworks = {
    item1: {
      bar: "bar1",
      framework: "frameworkName",
    },
  };

  return (
    <div className={details} style={{cursor: 'pointer'}} onClick={tilt} role="button">
      <a>
        <div className="background">
          <div className="card">
            <div className="photo"></div>
            <h2>{props.language}</h2>
            <p>{props.subtitle}</p>
            <div className="chart">{props.children}</div>
            <h3>{props.experience}</h3>
          </div>
          <div class="info">{props.info}</div>
        </div>
      </a>
    </div>
  );
}

Skill.defaultProps = {
  language: "PHP",
  subtitle: "THIS WEEK",
  info: "Click me to see projects",
  experience: "20+ Projects",
};

export default Skill;
